* {
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

* a {
  text-decoration: none;
  color: inherit;
}

.profile {
  width: 100%;
  height: 100%;
}

.profile .profile_header {
  width: 100%;
  height: 100px;
  text-align: center;
  line-height: 100px;
}

.profile .profile_header > p {
  font-size: 1.5em;
  font-weight: bold;
}

.profile .profile_body {
  width: 100%;
  height: 300px;
  text-align: center;
}
