* {
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

* a {
  text-decoration: none;
  color: inherit;
}

* body {
  background-color: skyblue;
}

article {
  position: relative;
  margin-top: 100px;
  margin-bottom: 50px;
  position: relative;
  width: 1400px;
  height: 1000px;
}

article .template,
article .template-up {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: beige;
  box-shadow: 1px 2px 10px #919191;
  animation: template_slidedown 2s;
}

article .template-up {
  animation: template_slideup 1s forwards;
}

article .template-down {
  animation: template_slidedown 1s forwards;
}
