* {
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

* a {
  text-decoration: none;
  color: inherit;
}

* body {
  background-color: skyblue;
}

.MenuDiv {
  z-index: 100;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 1400px;
  height: 70px;
}

.MenuDiv .MenuBar,
.MenuDiv .MenuBar-up {
  width: 100%;
  height: 100%;
  animation: menubar_slidedown 1.5s;
  display: flex;
  background-color: white;
  box-shadow: 1px 2px 10px #919191;
}

.MenuDiv .MenuBar > li,
.MenuDiv .MenuBar-up > li {
  width: 200px;
  height: 70px;
  line-height: 70px;
  text-align: center;
}

.MenuDiv .MenuBar > li > p,
.MenuDiv .MenuBar-up > li > p {
  position: relative;
  font-size: 1vw;
  width: 100%;
  height: 100%;
  font-family: "Sofia";
}

.MenuDiv .MenuBar > li > p:hover,
.MenuDiv .MenuBar-up > li > p:hover {
  cursor: pointer;
  font-weight: bold;
  font-size: 1.1vw;
  transition: all 0.2s;
}

.MenuDiv .MenuBar > li > p > img,
.MenuDiv .MenuBar-up > li > p > img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40%;
  height: 80%;
}

.MenuDiv .MenuBar-up {
  animation: menubar_slideup 1.5s forwards;
}

.MenuDiv .MenuBar-down {
  animation: menubar_slidedown 1.5s forwards;
}

@font-face {
  font-family: "Sofia";
  src: url(../fonts/Sofia_Sans/SofiaSans-Italic-VariableFont_wght.ttf);
}
